body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #000000;
  color: #000;
}

.App {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.App-header {
  background-color: #ffffff;
  color: #000000;
  padding: 10px 0;
  height: 80px;
  position: sticky;
  top: 0;
  z-index: 100;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.App-header h1 {
  font-size: 24px; /* Original: 24px */
}

.App-header ul {
  font-size: 16px; /* Original: 20px */
}

nav ul {
  list-style: none;
}

nav ul li {
  display: inline;
  margin-right: 20px;
}

nav ul li a {
  color: #000000;
  text-decoration: none;
  transition: color 0.3s ease;
}

nav ul li a:hover {
  color: #555555;
}

.App-section {
  position: sticky;
  top: 40vh; /* adjust the value to your liking */
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin-top: -50px;
}

.blue-bg {
  background-color: #dadada;
  color: #4e4d4d;
}

.App-footer {
  background-color: #ffffff;
  color: #000000;
  padding: 10px 0;
  position: fixed;
  bottom: 0;
  width: 100%;
  text-align: center;
  height: 130px;
}


.container {
  max-width: 960px;
  margin: 0 auto;
  padding: 20px;
  height: 80vh;
}

.container h2 {
  font-size: 20px; /* Adjusted: 20px */
}

.container p {
  font-size: 14px; /* Adjusted: 16px */
}

#about {
  height: calc(100vh - 80px); /* Adjusted height */
  overflow: hidden;
}

/* Media queries for smaller devices */
@media only screen and (max-width: 768px) {
  .App-header h1 {
    font-size: 20px; /* Original: 20px */
  }

  .container {
    padding: 10px;
  }
}


/* Dark mode styles */
.dark-mode {
  background-color: #000000;
  color: #ffffff;
}

.dark-mode .App-header {
  background-color: #000000; /* Change header background color to black */
  color: #ffffff;
}

.dark-mode .App-section {
  background-color: #222222; /* Change about me section background color to dark gray */
  color: #ffffff;
}

.dark-mode .App-footer {
  background-color: #000000; /* Change footer background color to black */
  color: #ffffff;
}

.dark-mode nav ul li a {
  color: #ffffff;
}

.dark-mode nav ul li a:hover {
  color: #a4a4a4;
}


/* Button styles */
.mode-button {
  background-color: #dbdbdb;
  color: #1c1c1c;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  margin-top: 20px;
  border-radius: 5px;
}

.mode-button:hover {
  background-color: #b0b0b0;
}

/* Light mode button in dark mode */
.dark-mode .mode-button {
  background-color: #ffffff; /* Change button background color to white */
  color: #000000; /* Change button text color to black */
}

.dark-mode .mode-button:hover {
  background-color: #dcdcdc; /* Change button hover background color to light gray */
}
